/* simulator */
@media (min-width: 1300) {
  body {
    min-width: 1100px;
  }
}
body {
  margin: 0;
  color: #333;
  font-size: 14px;
  overflow-x: auto;
  background-color: #fff;
  -webkit-font-smoothing: antialiased;
  font-family: PingFang SC, "Helvetica Neue", Arial, sans-serif;
}
p {
  margin: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-size: inherit;
}
ul,
ol {
  margin: 0;
  padding: 0;
  list-style: none;
}
a {
  text-decoration: none;
}
.panda-doc-row {
  width: 100%;
}
@media (min-width: 1440px) {
  .panda-doc-row {
    width: 1440px;
    margin: 0 auto;
  }
}
.panda-doc-header {
  width: 100%;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  border-bottom: 1px solid #f1f4f8;
}
.panda-doc-header__logo {
  display: flex;
  align-items: center;
}
.panda-doc-header__logo img {
  vertical-align: middle;
  height: 30px;
  margin-right: 10px;
}
.panda-doc-header__logo span {
  font-size: 20px;
}
.panda-doc-header__logo span.version {
  font-size: 90%;
  padding-left: 5px;
  padding-top: 7px;
  opacity: 0.7;
  margin-left: 3px;
  line-height: 1;
}
.panda-doc-header__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #fff;
  padding: 0 40px;
  height: 60px;
  line-height: 60px;
}
.panda-doc-header__top-nav-title {
  display: block;
}
.panda-doc-header__top-nav-title svg {
  fill: #455a64;
  display: block;
  vertical-align: middle;
  transition: 0.3s ease-in-out;
}
.panda-doc-header__top-nav-title svg:hover {
  fill: #0079f3;
}
.panda-doc-nav {
  left: 0;
  top: 60px;
  bottom: 0;
  z-index: 1;
  position: fixed;
  overflow-y: scroll;
  padding: 25px 0 75px;
  min-width: 240px;
  max-width: 240px;
  border-right: 1px solid #f1f4f8;
}
@media (max-width: 1300px) {
  .panda-doc-nav {
    min-width: 220px;
    max-width: 220px;
  }
}
@media (min-width: 1440px) {
  .panda-doc-nav {
    left: 50%;
    margin-left: calc(-720px);
  }
}
.panda-doc-nav::-webkit-scrollbar {
  height: 6px;
  width: 6px;
  background-color: transparent;
}
.panda-doc-nav::-webkit-scrollbar-thumb {
  border-radius: 6px;
  background-color: transparent;
}
.panda-doc-nav:hover::-webkit-scrollbar-thumb {
  background-color: rgba(69, 90, 100, 0.2);
}
.panda-doc-nav__item a,
.panda-doc-nav__subitem a {
  margin: 0;
  display: block;
  color: #455a64;
  font-size: 16px;
  padding: 10px calc(20px) 10px 40px;
  line-height: 24px;
  transition: all .3s;
}
.panda-doc-nav__item a.active,
.panda-doc-nav__subitem a.active {
  color: #0079f3;
}
.panda-doc-nav__item > a {
  font-weight: bold;
}
.panda-doc-nav__subitem a {
  font-size: 14px;
}
.panda-doc-nav__subitem a:hover {
  color: #0079f3;
}
.panda-doc-nav__subitem span {
  opacity: .6;
  font-size: 13px;
}
.panda-doc-nav__group-title {
  font-size: 12px;
  line-height: 40px;
  padding-left: 40px;
  color: rgba(69, 90, 100, 0.6);
}
@media (max-width: 1300px) {
  .panda-doc-nav {
    min-width: 220px;
    max-width: 220px;
  }
  .panda-doc-nav__item a,
  .panda-doc-nav__subitem a {
    line-height: 22px;
  }
  .panda-doc-nav__subitem a {
    font-size: 13px;
  }
}
.panda-doc-container {
  overflow: hidden;
  box-sizing: border-box;
  background-color: #fff;
  padding-left: 240px;
}
.panda-doc-container--with-simulator {
  padding-right: calc(400px);
}
@media (max-width: 1300px) {
  .panda-doc-container--with-simulator {
    padding-right: calc(360px);
  }
}
.panda-doc-content {
  flex: 1;
  position: relative;
  padding: 0 0 75px;
}
.panda-doc-content a {
  color: #0079f3;
}
.panda-doc-content section {
  padding: 10px 40px;
  overflow: hidden;
}
.panda-doc-content section .hljs {
  display: block;
  overflow-x: auto;
  padding: 0.5em;
  color: #333;
  background: #f8f8f8;
}
.panda-doc-content section .hljs-comment,
.panda-doc-content section .hljs-quote {
  color: #998;
  font-style: italic;
}
.panda-doc-content section .hljs-keyword,
.panda-doc-content section .hljs-selector-tag,
.panda-doc-content section .hljs-subst {
  color: #333;
  font-weight: bold;
}
.panda-doc-content section .hljs-number,
.panda-doc-content section .hljs-literal,
.panda-doc-content section .hljs-variable,
.panda-doc-content section .hljs-template-variable,
.panda-doc-content section .hljs-tag .hljs-attr {
  color: #008080;
}
.panda-doc-content section .hljs-string,
.panda-doc-content section .hljs-doctag {
  color: #d14;
}
.panda-doc-content section .hljs-title,
.panda-doc-content section .hljs-section,
.panda-doc-content section .hljs-selector-id {
  color: #900;
  font-weight: bold;
}
.panda-doc-content section .hljs-subst {
  font-weight: normal;
}
.panda-doc-content section .hljs-type,
.panda-doc-content section .hljs-class .hljs-title {
  color: #458;
  font-weight: bold;
}
.panda-doc-content section .hljs-tag,
.panda-doc-content section .hljs-name,
.panda-doc-content section .hljs-attribute {
  color: #000080;
  font-weight: normal;
}
.panda-doc-content section .hljs-regexp,
.panda-doc-content section .hljs-link {
  color: #009926;
}
.panda-doc-content section .hljs-symbol,
.panda-doc-content section .hljs-bullet {
  color: #990073;
}
.panda-doc-content section .hljs-built_in,
.panda-doc-content section .hljs-builtin-name {
  color: #0086b3;
}
.panda-doc-content section .hljs-meta {
  color: #999;
  font-weight: bold;
}
.panda-doc-content section .hljs-deletion {
  background: #fdd;
}
.panda-doc-content section .hljs-addition {
  background: #dfd;
}
.panda-doc-content section .hljs-emphasis {
  font-style: italic;
}
.panda-doc-content section .hljs-strong {
  font-weight: bold;
}
.panda-doc-content section code {
  font-size: 13px;
  overflow-x: auto;
  font-weight: 400;
  line-height: 22px;
  border-radius: 3px;
  margin-bottom: 25px;
  position: relative;
  word-break: break-all;
  white-space: pre-wrap;
  color: #455a64;
  padding: 18px 10px 18px 20px;
  background-color: #f1f4f8;
  font-family: Source Code Pro,Monaco,Inconsolata,monospace;
}
.panda-doc-content section > h1,
.panda-doc-content section > h2,
.panda-doc-content section > h3,
.panda-doc-content section > h4,
.panda-doc-content section > h5,
.panda-doc-content section > h6 {
  line-height: 1.5;
  font-weight: normal;
  margin: 20px 0 10px;
  color: #333;
}
.panda-doc-content section > h1 {
  font-size: 36px;
}
.panda-doc-content section > h2 {
  font-size: 30px;
  margin-bottom: 25px;
}
.panda-doc-content section > h3 {
  font-size: 22px;
  margin-top: 45px;
}
.panda-doc-content section > h2 + h3 {
  margin-top: 25px;
}
.panda-doc-content section > h4 {
  font-size: 16px;
  margin-bottom: 15px;
}
.panda-doc-content section > h5 {
  font-size: 14px;
}
.panda-doc-content section > h6 {
  font-size: 14px;
  color: #666;
}
.panda-doc-content section > p {
  margin: 15px 0;
  font-size: 14px;
  line-height: 26px;
  color: #34495e;
}
.panda-doc-content section > ul,
.panda-doc-content section > ol {
  padding: 15px 0;
  background-color: #f1f4f8;
}
.panda-doc-content section > ul li,
.panda-doc-content section > ol li {
  color: #34495e;
  font-size: 14px;
  line-height: 22px;
  margin: 5px 0 5px 20px;
  padding-left: 15px;
  position: relative;
}
.panda-doc-content section > ul li::before,
.panda-doc-content section > ol li::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 6px;
  height: 6px;
  margin-top: 8px;
  border-radius: 50%;
  box-sizing: border-box;
  border: 1px solid #666;
}
.panda-doc-content section > ul li li,
.panda-doc-content section > ol li li {
  margin-left: 0;
}
.panda-doc-content section > hr {
  border: 0px none;
  border-top: 1px solid #eee;
}
.panda-doc-content section p > code,
.panda-doc-content section li > code,
.panda-doc-content section table code {
  margin: 2px;
  padding: 2px 7px;
  display: inline;
}
.panda-doc-content blockquote {
  padding: 16px;
  margin: 20px 0;
  font-size: 14px;
  border-radius: 4px;
  background-color: #ecf9ff;
  color: rgba(52, 73, 94, 0.8);
  border-left: 5px solid #50bfff;
}
.panda-doc-content table {
  width: 100%;
  font-size: 13px;
  line-height: 1.5;
  margin-bottom: 45px;
  background-color: #fff;
  border-collapse: collapse;
  color: #34495e;
}
.panda-doc-content table th {
  padding: 8px 10px;
  text-align: left;
  font-weight: normal;
  background-color: #f1f4f8;
}
.panda-doc-content table th:first-child {
  padding-left: 10px;
}
.panda-doc-content table td {
  padding: 8px;
  border-bottom: 1px solid #f1f4f8;
}
.panda-doc-content table code {
  font-size: 13px;
  padding: 0 8px;
  font-family: inherit;
  word-break: keep-all;
  display: block;
}
.panda-doc-content--changelog section {
  padding-bottom: 30px;
}
.panda-doc-content--changelog section > h2 {
  margin-top: 50px;
}
.panda-doc-content--changelog section > h2 + p {
  margin: 0;
}
.panda-doc-content--changelog section > h2 + p code {
  margin: 0;
}
.panda-doc-content--changelog section > h2 a {
  color: inherit;
  font-size: 24px;
  -webkit-font-smoothing: auto;
}
.panda-doc-content--changelog section > h2:nth-child(1),
.panda-doc-content--changelog section > h2:nth-child(2) {
  margin-top: 20px;
}
.panda-doc-content--changelog section > h2:nth-child(2) {
  margin-bottom: 10px;
}
.panda-doc-simulator {
  z-index: 1;
  overflow: hidden;
  position: absolute;
  border-radius: 6px;
  background: #fafafa;
  box-sizing: border-box;
  right: 40px;
  width: 360px;
  min-width: 360px;
  top: calc(100px);
  box-shadow: rgba(0, 0, 0, 0.2) 0px 1px 4px, rgba(0, 0, 0, 0.2) 0px 1px 2px;
}
@media (max-width: 1300px) {
  .panda-doc-simulator {
    width: 320px;
    min-width: 320px;
  }
}
@media (max-width: 1100px) {
  .panda-doc-simulator {
    left: 750px;
    right: auto;
  }
}
@media (min-width: 1440px) {
  .panda-doc-simulator {
    right: 50%;
    margin-right: calc(-680px);
  }
}
.panda-doc-simulator-fixed {
  position: fixed;
  top: 40px;
  z-index: 1000;
}
.panda-doc-simulator iframe {
  width: 100%;
  display: block;
}
.panda-doc-simulator__nav {
  height: 60px;
  background: url('data:image/png;base64,') no-repeat;
  background-size: 100%;
}
.panda-doc-simulator__url {
  left: 40px;
  top: 23px;
  right: 40px;
  font-size: 14px;
  position: absolute;
  text-align: center;
  font-weight: bold;
  line-height: 28px;
  white-space: nowrap;
  overflow: hidden;
  font-family: PingFang SC, Helvetica Neue, Helvetica, Arial, sans-serif;
}
@media (max-width: 1300px) {
  .panda-doc-simulator__url {
    top: 21px;
    line-height: 24px;
  }
}
.panda-doc-simulator__reload {
  top: 25px;
  right: 10px;
  width: 28px;
  height: 28px;
  cursor: pointer;
  position: absolute;
}
